<template>
	<div>
		<div class="prddetail-group">
			<h3 class="prddetail-title border-bottom"></h3>
			<div class="ticketcontent">
					<div class="ticketcontent-item">
						<p class="ticketcontent-title"></p>
							<div class="ticketcontent-line">
								<div class="ticketcontent-keyword"></div>
								<div class="ticketcontent-con">
									<div class="ellipsis">
										<span class="ticketcontent-light">14:30</span>
									</div>
									<p class="ticketcontent-desc">14:30</p>
								</div>
							</div>
							<div class="ticketcontent-line">
								<div class="ticketcontent-keyword">  </div>
								<div class="ticketcontent-con">
									<p class="ticketcontent-desc">09:00~14:30</p>
								</div>
							</div>
							<div class="ticketcontent-line">
								<div class="ticketcontent-keyword"></div>
								<div class="ticketcontent-con">
									<p class="ticketcontent-desc">10</p>
								</div>
							</div>
					</div>

					<div class="ticketcontent-item">
						<p class="ticketcontent-title"></p>
							<div class="ticketcontent-line">
								<div class="ticketcontent-keyword"></div>
								<div class="ticketcontent-con">
									<div class="ticketcontent-lightcon ellipsis">
										<span class="ticketcontent-light"></span>
									</div>
								</div>
							</div>
									<p class="ticketcontent-sepline">
										<span class="ticketcontent-linetag"><img class="ticketcontent-innericon" src="//img1.qunarzz.com/piao/fusion/1703/4f/60b1629ae5d22702.png"></span>)
									</p>
									<p class="ticketcontent-sepline">
										<span class="ticketcontent-linetag"><img class="ticketcontent-innericon" src="//img1.qunarzz.com/piao/fusion/1703/e2/8f4c1d0b353c9402.png"></span>
									</p>

							<p class="ticketcontent-line"><span class="ticketcontent-keyword"></span></p>
							<p class="ticketcontent-desc">1</p>

							<p class="ticketcontent-line"><span class="ticketcontent-keyword"></span></p>
							<p class="ticketcontent-desc">08:4517:3017:30<br>09:0016:4516:30</p>
						


					</div>


					<div class="ticketcontent-item">
						<p class="ticketcontent-title"></p>
							<div class="ticketcontent-line">
								<div class="ticketcontent-keyword"></div>
								<div class="ticketcontent-con">
									<div class="ellipsis">
										<span class="ticketcontent-light"></span>
									</div>
									<p class="ticketcontent-desc">3000:00<br></p>
								</div>
							</div>
							<div class="ticketcontent-line">
								<div class="ticketcontent-keyword"></div>
								<div class="ticketcontent-con">
									<p class="ticketcontent-desc"></p>
								</div>
							</div>
					</div>

					<div class="ticketcontent-item">
						<p class="ticketcontent-title"></p>
						<p class="ticketcontent-line" role="feeDesc">1+++++</p>
					</div>

						<div class="ticketcontent-item" style="display:none;" role="usingDescArea">
							<p class="ticketcontent-title"></p>
							<p class="ticketcontent-line"></p>
						</div>
			   
				<div class="ticketcontent-more" role="useDescMoreBtn" style="display: block;">
					<div role="moreShaddow" class="ticketcontent-shadow"></div>
					<span role="moreText"></span><span class="iconfont ticketcontent-arrow" role="useDescMoreArrow">E</span>
				</div>
			</div>
		</div>	
		<div class="prddetail-group">
			<h3 class="prddetail-title"></h3>
			<div class="prddetail-content border-top">
				<div class="sightlist">
					<span class="sightlist-item border-left">
						<router-link to="/product" class="sightlist-link">
						<span class="iconfont sightlist-temple"></span>
						(4A)</router-link></span>
				</div>
			</div>
		</div>
		<div class="prddetail-group" style="position:relative">
			<h3 class="prddetail-title"></h3>
			<div class="prddetail-content border-top">
				<div class="sullier" :class="supplyShow ? 'ellipsis2' : '' ">
					10
				</div>
			</div>

			<div class="supplier-more" @click="showSupply">
				<div class="supplier-shadow" v-show="supplyShow"></div>
				<span><span class="iconfont supplier-arrow">E</span>
			</span></div>

		</div>
		<div class="risktip-con" :class="riskShow ? 'risktip-con-fold' : ''">
			<div class="risktip-titlecon" @click="showRisk">
				<span class="risktip-arrowright mpg-iconfont">F</span>
			</div>
			<div class="risktip-content">
			   1.<a target="_blank" href="https://yun.qunar.com/uploads2/notice/20180801/HighRiskProjectWarning.pdf " class="risklink" role="riskLink"></a><br>2.<a target="_blank" href="https://yun.qunar.com/uploads2/notice/20180711/travelnotes.pdf " class="risklink" role="riskLink"></a>
			</div>
		</div>		
	</div>
</template>

<script>
export default{
	name: 'TicketPrddetail',
	data () {
		return {
			riskShow: true,
			supplyShow: true
		}
	},
	methods: {
		showRisk () {
			this.riskShow = !this.riskShow
		},
		showSupply () {
			this.supplyShow = !this.supplyShow
		}
	}
}
</script>

<style lang="stylus" scoped>
@import "~styles/varibles"
.ellipsis2
	display: -webkit-box
	overflow: hidden
	text-overflow: ellipsis
	-webkit-line-clamp: 2
	-webkit-box-orient: vertical
.prddetail-group
	margin-top: .2rem
	padding: .01rem 0
	background-color: $whiteTextColor
	.prddetail-title
		margin-left: .2rem
		padding: .2rem 0
		color: $sixOneColor
		font-size: .32rem
		line-height: .44rem
	.ticketcontent
		position: relative
		padding: 0 .38rem .3rem .38rem
		.ticketcontent-item
			padding-bottom: .2rem
			.ticketcontent-title
				position: relative
				margin-top: .3rem
				height: .48rem
				line-height: .48rem
				color: $twoOneColor
				font-size: .34rem
				&:before
					content: ""
					position: absolute
					top: 50%
					left: -.18rem
					width: .06rem
					height: .32rem
					background: #00bcd4
					border-radius: .06rem
					transform: translateY(-.16rem)
			.ticketcontent-line,.ticketcontent-sepline
				overflow: hidden
				margin-top: .2rem
				color: $sixOneColor
				font-size: .28rem
				line-height: .4rem
				word-break: break-all
				word-wrap: break-word
				.ticketcontent-keyword
					float: left
					width: 1.4rem
					color: $twoOneColor
					text-align: justify
				.ticketcontent-con
					overflow: hidden
					.ticketcontent-light
						display: inline-block
						height: .38rem
						padding: 0 .1rem
						color: $whiteTextColor
						line-height: .38rem
						border-radius: .02rem
						background-image: linear-gradient(130deg,#ffab1e 37%,#ff8c12 100%)
					.ticketcontent-desc
						color: $sixOneColor
						font-size: .28rem
						line-height: .4rem
						word-break: break-all
						word-wrap: break-word
						&:not(:first-child)
							margin-top: .08rem
			.ticketcontent-sepline
				position: relative
				padding-left: .4rem
				.ticketcontent-linetag
					position: absolute
					left: 0
					top: 0
					width: .4rem
					height: .4rem
					text-align: left
					.ticketcontent-innericon
						width: .26rem
						height: .26rem
						vertical-align: top
						margin-top: .06rem
		.ticketcontent-more
			display: block
			padding-top: .3rem
			text-align: center
			border-top: 0
			font-size: .28rem
			color: $sixOneColor
.ticketcontent-shadow,.supplier-shadow
	position: absolute
	left: 0
	right: 0
	height: .5rem
	background-image: linear-gradient(to bottom,rgba(255,255,255,0),#fff)
.ticketcontent-shadow
	bottom: .8rem
.ticketcontent-arrow
	font-size: .24rem
	margin-left: .05rem
.prddetail-content
	color: $nineEColor
	font-size: .28rem
	line-height: .44rem
	overflow: hidden
	padding: .15rem 0
	margin: 0 .2rem
.sightlist
	overflow: hidden
	overflow-x: auto
	white-space: nowrap
	.sightlist-item
		display: inline-block
		padding: 0 .4rem
		margin: .15rem 0
		line-height: .4rem
		font-size: .28rem
		&:first-of-type
			padding-left: .2rem
			&:before
				border-color: #ffffff
		&:last-of-type
			padding-left: .2rem
	.sightlist-link
		color: $twoOneColor
		.sightlist-temple
			color: #bdbdbd
			position: relative
			top: .02rem
			margin-right: .1rem
.supplier-more
	padding: .06rem 0 .2rem 0
	text-align: center
	border-top: 0
	font-size: .28rem
	color: $sixOneColor
.supplier-shadow
	bottom: .6rem
.supplier-arrow
	font-size: .24rem
	margin-left: .05rem
.prddetail-arrowup
	display: inline-block
	color: $nineEColor
	transform: rotate(180deg)
.risktip-con-fold
	height: 1rem
	overflow: hidden
.risktip-con
	background: #ffffff
	margin: .2rem 0
	padding: 0 .24rem
	color: $sixOneColor
	.risktip-titlecon
		height: 1rem
		line-height: 1rem
		color: $twoOneColor
		font-size: .34rem
		.risktip-arrowright
			float: right
			height: 1rem
			line-height: 1rem
			color: $nineEColor
			font-size: .24rem
.risktip-content
	line-height: .4rem
	margin-bottom: .3rem
	display: inline-block
.risklink
	color: #00bcd4
	text-decoration: none
</style>



